/* eslint-disable @typescript-eslint/ban-ts-comment */
import { constants } from '@ali/4ever-cangjie';

const leftThrottle = 100;
const rightThrottle = 30;
const { leafBlock: leafBlockKey, key: cangjieKey, edgeSelectable, content: contentKey } = constants.Selector;

const COLUMN_SELECTOR = 'data-column';
const VOID_BLOCK = 'data-block-uuid';

// 如果鼠标在 content 左侧，则往右侧偏移一部分，取新的元素。
const RIGHT_OFFSET = 20;

/**
 * 从 target 开始，往 parent 找，一直找到并返回满足 attribute 的元素
 */
function getParentByAttributes(attribute: string | string[], target: HTMLElement | null) {
  const attributes = Array.isArray(attribute) ? attribute : [attribute];
  let found: HTMLElement | null = target;
  while (
    found &&
    !found.getAttribute(contentKey) &&
    found !== window.document.body &&
    found !== window.document.documentElement
  ) {
    for (let i = 0; i < attributes.length; i++) {
      if (found.getAttribute(attributes[i])) return found;
    }
    found = found.parentElement;
  }
  return null;
}

export default function findInnerBlockKeyFromEvent(event: MouseEvent, container: Element | null): string {
  const eventTarget = event.target as HTMLElement | null;
  if (!eventTarget) {
    return '';
  }
  let oriTarget: Element = eventTarget;
  let blockKey = '';

  if (container) {
    const paddingLeft = parseInt(getComputedStyle(container).paddingLeft, 10) || leftThrottle;
    const rect = container.getBoundingClientRect();
    const { clientX, clientY } = event;
    if (clientX > rect.left && clientX < rect.left + paddingLeft) {
      oriTarget = window.document.elementFromPoint(rect.left + paddingLeft + RIGHT_OFFSET, clientY) || eventTarget;
    }
    if (clientX > rect.right && clientX < rect.right + rightThrottle) {
      oriTarget = window.document.elementFromPoint(rect.right - rect.width / 2, clientY) || eventTarget;
    }
  }

  let target = oriTarget as HTMLElement | null;
  const blockEle = getParentByAttributes(['data-listid', leafBlockKey, VOID_BLOCK, COLUMN_SELECTOR, edgeSelectable], target);

  if (blockEle) {
    blockKey =
      blockEle.getAttribute(cangjieKey) ||
      blockEle.querySelector(`[${cangjieKey}]`)?.getAttribute(cangjieKey) || '';
  }
  return blockKey;


  

}
